*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 12px;
}

body {
  background-color: #f8f9fb;
  font-family: 'Mukta Mahee', sans-serif;
}

p {
  font-size: 1.3rem;
  line-height: 120%;
}

h2 {
  font-size: 2rem;
}

.container {
  overflow: hidden;
  width: 70rem;
  min-height: 43rem;
  display: grid;
  grid-template-columns: 22rem 1fr;
  margin: 5% auto;
  border-radius: 1rem;
  box-shadow: 0 7px 30px rgba(52, 31, 97, 0.1);
}

.sidebar {
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: white;
  padding: 2rem;
}

.content {
  background-color: lightsteelblue;
  padding: 2rem 2.5rem;
}

.subcontent {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.avatar {
  width: 160px;
  margin-bottom: 2rem;
  border-radius: 50%;
}

.cv-field {
  display: flex;
  flex-direction: column;
  min-width: 17.8rem;
  /* border: 1px solid green; */
}

.cv-field > div {
  margin-bottom: 0.7rem;
}

.cv-field-title {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  position: relative;
  margin-bottom: 1rem;
}

.edit-btn {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 2rem;
  margin-left: 1rem;
}

.cv-field-title:hover > .edit-btn {
  display: block;
}

.cv-field-title::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -0.1rem;
  background-color: #2f2e41;
  width: 20%;
  height: 3px;
}

.cv-detail {
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: bold;
}

.name {
  margin-bottom: 2rem;
  position: relative;
  display: flex;
}

.name-form {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 68px;
  /* border: 1px solid black; */
}

.name-form input {
  height: 100%;
  width: 80%;
  background: none;
  font-size: 3.5rem;
  font-weight: 700;
  color: rgba(0, 0, 0, 0.4);
  font-family: 'Mukta Mahee', sans-serif;
  border: none;
  /* border: 1px solid red; */
  outline: none;
}

.cv-field-form {
  min-width: 17.8rem;
}

.cv-field-form div {
  height: 4.2rem;
  /* border: 1px solid red; */
}

.cv-field-form div label input {
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  color: rgba(0, 0, 0, 0.4);
  font-family: 'Mukta Mahee', sans-serif;
  font-size: 1.3rem;
  display: block;
  height: 40%;
  width: 80%;
}

.submit-btn {
  border: none;
  background-color: #f8f9fb;
  cursor: pointer;
  font-family: 'Mukta Mahee', sans-serif;
  font-weight: 700;
  font-size: 1.2rem;
  padding: 0 0.4rem;
  margin-right: 1rem;
  border-radius: 5px;
}

.name h1 {
  text-transform: uppercase;
  font-size: 3.5rem;
}

.name:hover > .edit-btn {
  display: block;
}

.name::after {
  position: absolute;
  content: '';
  left: 0;
  bottom: -0.5rem;
  width: 100%;
  height: 4px;
  background-color: #2f2e41;
}
